import React, {Component} from 'react';
import Signin from '../components/Signin/';


class Index extends Component {
  constructor() {
    super();
    this.state = {
      loggedIn: false,
    };
  }
  render() {
    return (
      <div>
        <header>
          <h1>Welcome back, Username</h1>
          <div>
            <button>Signout</button>
          </div>
        </header>
        { this.state.loggedIn ? null : <Signin/> }
      </div>
    )
  }
}

export default Index;
